<template>
	<div class="details">
		<div class="top">
			<i class='icon9' @click='returnpage()'><</i>
			<p class='title'>{{data.title}}</p>
			<p class="btn">随机播放全部</p>
			<img :src="data.imgurl" alt="">
		</div>
		<ul class='list' v-if='data.data'>
			<li v-for='item,index in data.data.data' v-if='show[0]' @click='tiao(item,index)'>
				<p class='sing'>{{item.albumname}}</p>
				<p class='singer'>{{item.songname}}</p>
			</li>
			<li v-for='item,index in data.data.data.list' v-if='show[1]' @click='tiao2(item,index)'>
				<p class='sing'>{{item.musicData.songname}}</p>
				<p class='singer'>{{data.data.data.singer_name}}</p>
			</li>
			<li v-for='item,index in data.data.data.tracks' v-if='show[2]' @click='tiao(item,index)'>
				<div class="icon" :class='getclass(index)'>
					<span v-if='index>2'>{{index+1}}</span>
				</div>
				<div class="xi">
					<p class='song'>{{item.name}}</p>
					<p class='sing'><span v-for='item1,index1 in item.singer'>{{item1.name}}&nbsp;&nbsp;</span></p>
				</div>

			</li>
		</ul>
	</div>
</template>
<script>
	import {mapMutations} from 'vuex';
	export default{
		data(){
			return{
				data: {},
				show: [
					false,
					false,
					false
				]
			}
		},
		methods: {
			...mapMutations({
				playList1: 'playList',
				curIndex1: 'curIndex',
				curSong1: 'curSong'
			}),
			returnpage(){
        this.$router.go(-1);
			},
			getclass(index){
				if(index<3){
					return 'icon'+(index+1);
				}else{
					return;
				}
			},
			tiao(item,index){
				this.playList1(this.data.data.data);
				this.curIndex1(index);
				this.curSong1(item);
				this.$router.push({
					path: '/play'
				})
			},
			tiao2(item,index){
				this.playList1(this.data.data.data.list);
				this.curIndex1(index);
				this.curSong1(item);
				this.$router.push({
					path: '/play'
				})
			}
		},
		mounted(){
			this.data = this.$route.params;
			this.show[this.$route.params.show] = true;
			this.data.data.data.list.forEach(item => {
						let obj = {
							images: `https://y.gtimg.cn/music/photo_new/T002R300x300M000${item.musicData.albummid}.jpg?max_age=2592000`,
	                        url: `http://audio04.dmhmusic.com/71_53_T10051194776_128_4_1_0_sdk-cpm/cn/0311/M00/26/F4/ChAKC113eE2ACpDZACG2YOBaUN8185.mp3?xcode=e23d93368f2b8d3d54199ac07c46c6ac547e127`
						}
						this.$set(item.musicData,'newData',obj)
					})
			console.log(this.data.data.data.list)
		}
	}
</script>
<style scoped>
	.icon{
		display: inline-block;
		color: rgb(255,223,54);
		font-size: 18px;
	}
	.icon1{
		width: 25px;
		height: 24px;
		background: url('/static/images/1.png');
		background-size: 25px 24px;
	}
	.icon2{
		width: 25px;
		height: 24px;
		background: url('/static/images/2.png');
		background-size: 25px 24px;
	}
	.icon3{
		width: 25px;
		height: 24px;
		background: url('/static/images/3.png');
		background-size: 25px 24px;
	}
	.xi{
		display: inline-block;
	}
	.details{
		width: 100vw;
		background: rgb(40,40,40);
	}
	.details .top {
		position: relative;
		display: flex;
		justify-content: center;
	}
	.details .top img{
		width: 100%;
		height: 100%;
	}
	.details .top .title{
		position: absolute;
		top: 0;
		color: #fff;
		font-weight: 600;
		font-size: 25px;
	}
	.details .top .icon9{
		position: absolute;
		left: 0;
		top: 0;
	}
	.details .top .icon9{
		font-size: 25px;
		color: rgb(255,223,54);
	}
	.details .top .btn{
		position: absolute;
		bottom: 20px;
		border: 2px solid rgb(255,238,50);
		color: rgb(255,238,50);
		width: 170px;
		height: 40px;
		text-align: center;
		line-height: 40px;
		border-radius: 20px;
	}
	.details .list li{
		padding-left: 20px;
		padding-top: 20px;
	}
	.details .list li p{
		color: #fff;
	}
	.details .list li .singer{
		font-weight: normal;
		color: rgb(120,120,120);
	}
</style>



